*{
    margin: 0;
    padding: 0;
    text-align: center;
    justify-content: center;
    display: flexbox;
}
body{
    background-color: #b4f2e1;
    box-sizing: border-box;
    margin: 0;
    padding: 0%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
div{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -30px;
}
h1{
    padding: 20px 20px;
    background-color: #ffe9c5;
    color: #fa9191;
    border-radius: 5px;
}
span{
    color: #eb6383;
}